.review {
	&__body {
		position: relative;
		h3 {
			max-width: 909px;
			margin: 0px auto;
			text-align: center;
		}
		.swiper {
			margin-top: 94px;
			&-slide {
				width: auto;
				display: flex;
				justify-content: center;
				align-items: center;
				overflow-y: auto;
				overflow-x: hidden;
				margin-bottom: 100px;
				.content {
					max-width: 472px;
					padding: 0px 20px;
					@media (max-width: 992px) {
						text-align: center;
						margin: 0px auto;
					}
					.review__contact {
						width: 259px;
						height: 64px;
						display: flex;
						justify-content: space-between;
						align-items: center;
						margin-bottom: 24px;
						text-align: left;
						@media (max-width: 992px) {
							width: 230px;
							margin: 0px auto 24px auto;
						}
						.subtext {
							color: #939191;
						}
					}
				}
			}
			&-pagination {
				display: flex;
				justify-content: center;
				align-items: center;
				&-bullet {
					width: 32px;
					height: 4px;
					background-color: #939191 !important;
					border-radius: 0;
					opacity: 1;
					&-active {
						background-color: #ff6433 !important;
					}
				}
			}
			&-nav {
				display: flex;
				align-items: center;
				height: 62px;
			}
			&-buttons {
				display: flex;
				align-items: center;
				justify-content: space-between;
				width: 170px;
				position: absolute;
				right: 0;
				bottom: 0;
				@media (max-width: 768px) {
					display: none;
				}
			}
			.swiper-prev, 
			.swiper-next {
				width: 62px;
				height: 62px;
				border-radius: 50%;
				display: flex;
				justify-content: center;
				align-items: center;
				cursor: pointer;
				transition: all 0.3s ease;
				&:hover {
					background-color: #000;
					transition: all 0.3s ease;
					path {
						fill: rgb(255, 255, 255);
						transition: all 0.3s ease;
					}
				}
			}
		}
	}
}
