.work{
	&__body{
		display: flex;
		flex-direction: column;
		gap: 128px;
		@media (max-width: 1110px){
			gap: 40px;
		}
	}
	&__item{
		display: flex;
		justify-content: space-between;
		gap: 141px;
		@media (max-width: 1182px){
			gap: 40px !important;
		}
		@media (max-width: 1110px){
			flex-direction: column !important;
			justify-content: center;
			align-items: center;
		}
		.work__text{
			max-width: 489px;
			@media (max-width: 1110px){
				max-width: 100%;
			}
			h3{
				margin-bottom: 48px;
			}	 
			.work__list{
				display: flex;
				align-items: center;
				gap: 20px;
				margin-bottom: 32px;
				@media (max-width: 400px){
					gap: 10px;
					margin-bottom: 20px;
					img{
						width: 30px;
					}
				}
			}
		}
		.work__img{
			display: flex;
			align-items: center;
			width: 100%;
			@media (max-width: 600px){
				flex-direction: column;
				img{
					margin-right: 0 !important;
					margin-bottom: 16px;
					&:last-child{
						margin-bottom: 0;
					}
				}
			}
			img{
				width: 100%;
				height: auto;
				margin-right: 16px;
				&:last-child{
					margin-right: 0;
				}
			}
		}
		.text{
			margin: 30px 0;
		}
		&:last-child{
			flex-direction: row-reverse;
			gap: 88px;
			h3{
				margin-bottom: 0;
			}
		}
	}
}