.header{
   position: relative;
   width: 100%;
   padding: 30px 0;
   @media (max-width: 992px){
      z-index: 3;
      transition: all .3s ease;	
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      background-color: #ffffff;
      padding: 20px 0;
      max-height: 70px;
      height: 100%;
      overflow-y: hidden;
      &.active{
         background-color: #ffffff;
         max-height: 100vh;
         @media (max-width: 992px) and (max-height: 500px){
            overflow-y: scroll;
            &::-webkit-scrollbar{
               display: none;
            }
         }
         .header__body{
            height: 100vh;
            @media (max-width: 992px) and (max-height: 500px){
               height: 450px;
            }
         }
      }
      &__logo{
         top: 21px;
         left: 20px;
         position: absolute;
      }
      &__burger{
         position: absolute;
         right: 20px;
         top: 25px;
         width: 30px;
         height: 20px;
         z-index: 3;
         cursor: pointer;
         &::after,
         &::before{
            content: '';
            position: absolute;
            width: 100%;
            height: 2px;
            left: 0px;
            background-color: #1E1B1B;
            transition: all .3s ease;
         }
         &.active::before{
            transform: rotate(45deg);
            top: 9px;
         }
         &.active::after{
            transform: rotate(-45deg);
            bottom: 9px;
         }
         &::before{
            top: 0px;
         }
         &::after{
            bottom: 1px;
         }
         span{
            position: absolute;
            background-color: #1E1B1B;
            left: 0px;
            width: 100%;
            height: 2px;
            top: 8px;
            transition: all .3s ease;
         }
         &.active span{
            transform: scale(0);
         }
      }	
   }
   &__body{
      display: flex;
      align-items: center;
      justify-content: space-between;
      @media (max-width: 992px){
        justify-content: center;
      }	
      .header__menu{		
         display: flex;
         align-items: center;
         gap: 64px;	
         @media (max-width: 992px){
            transition: all .3s ease;
            opacity: 0;
            visibility: hidden;
            flex-direction: column;
            &.active{
               opacity: 1;
               visibility: visible;
               transition: all .3s ease;
            }
         }
         .header__list{
            display: flex;
            align-items: center;
            gap: 40px;
            @media (max-width: 992px){
               flex-direction: column;
               gap: 20px;
            }
            li{
               position: relative;
               display: inline-block;
               a{
                  font-size: 18px;
                  line-height: 175%;
               }
               &::after{
                  content: '';
                  position: absolute;
                  left: 0;
                  bottom: -1px;
                  width: 100%;
                  height: 2px;
                  background-color: #1E1B1B;
                  transform: translate(-50%, 0) scale(0);
                  transition: all .3s ease;
               }
               &:hover::after{
                  transform: translate(0, 0) scale(1);
                  transition: all .3s ease;
               }
            }
         }
         .header__phone{
            display: flex;
            align-items: center;
            gap: 14px;
            .phone{
               picture{
                  display: flex;
                  align-items: center;
                  justify-content: center;
               }
               background-color: #61C4A1;
               width: 40px;
               height: 40px;
               display: flex;
               justify-content: center;
               align-items: center;
               border-radius: 50%;
               cursor: pointer;
            }
         }
      }		
   }
}