.faq{
	&__body{
		h3{ 
			text-align: center;
			margin-bottom: 95px;
			@media (max-width: 768px){
				margin-bottom: 40px;
			}
		}
		.accordion{
			width: 100%;
			&__item{
				max-width: 896px;
				margin: 0px auto;
				&.show{
					.accordion__content{
						height: 150px;
					}
					.accordion__header{
						.decor{
							.line1{
								transition: all .4s ease 0s;
								transform: rotate(90deg);
							}
						}
					}
				}
				.accordion__header{							
					height: 112px;
					display: flex;
					justify-content: space-between;
					align-items: center;
					position: relative;
					cursor: pointer;
					.decor{
						max-width: 51px;
						width: 100%;
						height: 48px;
						background-color: #FF6433;
						position: relative;
						z-index: 0;
						.line{
							background-color: #fff;
							height: 25px;
							width: 2px;
							position: absolute;
							left: calc(50% - 2px/2);
							top: calc(50% - 25px/2);
							transition: all .4s ease 0s;
						}
						.line2{
							transform: rotate(90deg);
						}
					}
					
				}	 
				.accordion__content  {
					height: 0;
					padding: 0px 110px 0px 40px;
					overflow: hidden;
					transition: .4s ease-in-out 0s;
					@media (max-width: 768px){
						padding: 0 50px 0 20px;
					}
					@media (max-width: 450px){
						padding: 0 50px 0 0;
					}
					@media (max-width: 400px){
						padding: 0 20px 0 0;
					}
					p{
						color: #939191;
                  font-weight: 500;
                  font-style: normal;
					}
				}
			}
		}
	}
}