.start { 
   position: relative;
   width: 100%;
   padding: 80px 0 128px;
   @media (max-width: 992px) {
      padding: 80px 0 50px;
   }
   &__service {
      width: 100%;
      @media (max-width: 992px) {
         margin-bottom: 0 !important;
         .container {
            max-width: 100% !important;
            padding: 0 !important;
         }
      }
      .start__body {
         display: flex;
         justify-content: center;
         height: 500px;
         @media (max-width: 992px) {
            position: relative;
            align-items: center;
         }
         &-img {
            flex: 0 1 40%;
            width: 100%;
            height: 500px;
            @media (max-width: 992px) {
               z-index: 0;
               flex: 0 1 100%;
            }
            img {
               width: 100%;
               height: 100%;
               object-fit: cover;
            }
         }
         .start__text {
            flex: 0 1 60%;
            width: 100%;
            height: 100%;
            padding: 96px;
            background-color: #1e1b1b;
            @media (max-width: 992px) {
               position: absolute;
               left: 0;
               top: 0;
               bottom: 0;
               right: 0;
               background-color: #1e1b1b96;
               flex: 0 1 100%;
               display: flex;
               justify-content: center;
               flex-direction: column;
            }
            @media (max-width: 768px) {
               padding: 50px 30px;
            }
            @media (max-width: 480px) {
               padding: 50px 20px;
            }
            h1,
            p {
               color: #fff;
            }
            h1 {
               margin-bottom: 16px;
            }
            p {
               margin-bottom: 48px;
            }
         }
      }
   }
   // blog
   &__blog {
      .start__body {
         height: 550px;
         background-color: #1e1b1b;
         @media (max-width: 992px) {
            flex-direction: column;
            height: auto;
         }
         .start__text {
            width: 40vw;
            height: 100%;
            background-image: url(../img/blog/start_img.webp);
            background-position: center center;
            background-repeat: no-repeat;
            background-size: auto;
            @media (max-width: 992px) {
               width: 100vw;
               position: relative;
               height: 200px;
            }
            @media (max-width: 480px) {
               height: 150px;
            }
            .content {
               display: flex;
               justify-content: center;
               align-items: center;
               width: 100%;
               height: 100%;
               background-color: rgba(35, 27, 27, 0.8);
               h1 {
                  color: #fff;
               }
            }
         }
         .start__swiper {
            max-width: 60%;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            @media (max-width: 992px) {
               max-width: 100%;
               padding: 30px 0;
            }
            .swiper-wrapper {
               width: 60vw;
               @media (max-width: 992px) {
                  width: 100vw;
               }
            }
            .swiper-slide {
               display: flex;
               justify-content: center;
               align-items: center;
               padding: 0px 40px;
               @media (max-width: 1280px) {
                  h4 {
                     font-size: calc(24px + (40 - 24) * ((100vw - 300px) / (1280 - 300)));
                  }
               }
               @media (max-width: 768px) {
                  padding: 0px 20px;
               }
               .content {
                  max-width: 614px;
                  height: 368px;
                  display: grid;
                  grid-row-gap: 16px;
                  p,
                  h4 {
                     color: #fff;
                  }
                  .subtext {
                     letter-spacing: 0.24em;
                     color: #ff6433;
                     font-weight: 500;
                     font-style: normal;
                  }
                  .start__info {
                     margin-top: 32px;
                     display: flex;
                     align-items: center;
                     @media (max-width: 400px) {
                        width: 100%;
                        justify-content: space-between;
                        a {
                           margin-right: 20px !important;
                        }
                     }
                     @media (max-width: 350px) {
                        flex-direction: column-reverse;
                        margin-top: 15px;
                        align-items: flex-start;
                        a {
                           margin-top: 10px;
                        }
                     }
                     a {
                        margin-right: 32px;
                        width: 172px;
                        height: 55px;
                        padding: 0;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                     }
                  }
               }
            }
         }
      }
   }
   &__body {
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media (max-width: 992px) {
         flex-direction: column;
         justify-content: center;
         gap: 50px;
      }
      .start__text {
         max-width: 624px;
         height: auto;
         @media (max-width: 1280px) {
            max-width: 550px;
         }
         @media (max-width: 1110px) {
            max-width: 500px;
         }
         @media (max-width: 992px) {
            max-width: 100%;
            text-align: center;
         }
         h1 {
            margin-bottom: 32px;
         }
         .start__time {
            margin-top: 48px;
            .caption {
               color: #939191;
            }
         }
      }
      .start__img {
         position: relative;
         .img {
            height: 100%;
            width: 100%;
            margin-left: 30px;
            @media (max-width: 992px) {
               margin-left: 0;
            }
         }
         .start__item {
            background: linear-gradient(138.85deg, #ffffff -38.72%, #ffffff 153.95%);
            border-radius: 59px;
            position: absolute;
            @media (max-width: 480px) {
               left: 50% !important;
               right: auto;
               transform: translate(-50%, 0);
               width: 80%;
            }
            .star {
               width: 100%;
               height: auto;
               img {
                  width: 100%;
                  height: 100%;
               }
            }
            .start__button {
               padding: 15px 25px;
               display: flex;
               justify-content: center;
               align-items: center;
               position: relative;
               @media (max-width: 350px) {
                  padding: 15px;
               }
               &::after {
                  content: "";
                  position: absolute;
                  left: 50%;
                  top: 50%;
                  transform: translate(-50%, -50%);
                  width: 100%;
                  height: 100%;
                  background-color: rgba(255, 255, 255, 0.43);
                  animation: ring 2.5s infinite;
                  border-radius: 59px;
                  z-index: 0;
                  @keyframes ring {
                     0% {
                        width: 100%;
                        height: 100%;
                     }
                     50% {
                        width: 110%;
                        height: 130%;
                     }
                     100% {
                        width: 100%;
                        height: 100%;
                     }
                  }
               }
            }
            .time {
               padding: 17px 13px;
               background-color: #61c4a1;
               border-radius: 50%;
               display: flex;
               justify-content: center;
               align-items: center;
               margin-right: 15px;
               z-index: 2;
               p {
                  color: #fff;
                  margin: 0px 3px 0px 0px;
               }
            }
            .subtitle,
            .rate,
            .star {
               z-index: 2;
            }
         }
         .start__item:nth-child(2) {
            position: absolute;
            top: 80px;
            left: -30px;
            @media (max-width: 480px) {
               top: 20px;
            }
         }
         .start__item:nth-child(3) {
            position: absolute;
            bottom: 50px;
            right: -20px;
            @media (max-width: 480px) {
               bottom: 20px;
            }
         }
      }
   }
}
