.team{
	h1{
		text-align: center;
		margin-bottom: 66px;
	}
	&__items{
		display: grid;
		grid-template-columns: repeat(auto-fit,minmax(300px, 1fr));
		grid-template-rows: repeat(2, 1fr);
		grid-column-gap: 32px;
		grid-row-gap: 48px;
		@media (max-width: 350px){
			grid-template-columns: repeat(auto-fit,minmax(100%, 1fr));
		} 
		.team__item{
			background-position: center center;
			background-repeat: no-repeat;
			background-size: cover ;
			background-image: url(../img/about/team_1.webp);
			height: 430px;
			width: 100%;
			position: relative;
			transition: all .3s ease !important;
			&:hover{
				background-size: 120%;
				transition: all .3s ease !important;
			}
			&:nth-child(2){
				background-image: url(../img/about/team_2.webp);
				background-position: center top;
			}
			&:nth-child(3){
				background-image: url(../img/about/team_3.webp);
			}
			&:nth-child(4){
				background-image: url(../img/about/team_4.webp);
			}
			&:nth-child(5){
				background-image: url(../img/about/team_5.webp);
			}
			&:last-child{
				background-image: url(../img/about/team_6.webp);
			}
			.team__text{
				width: 100%;
				height: 116px;
				position: absolute;
				left: 0;
				bottom: 0;
				display: flex;
				justify-content: center;
				align-items: center;
				background: #161616;
				p{
					color: #fff;
					text-align: center;
				}
				.subtext{
					color: rgba(255, 255, 255, 0.7);
				}
			}
		}
	}
}