.service{
	&__body{
		position: relative; 
		.service__text{
			max-width: 858px;
			margin: 0px auto;
			text-align: center
		}
		.service__items{
			grid-template-columns: repeat(auto-fit,minmax(250px, 1fr));;
			margin: 64px 0px 32px 0px;
			gap: 8px;
			width: 100%;
			@media (max-width: 768px){
				margin: 32px 0px 32px 0px;
			}
		}
		.link{
			position: absolute;
			right: 0;
			img{
				height: 18px;
			}
			@media (max-width: 768px){
				position: relative;
				justify-content: center;
			}
		}
	}
}