.steps{
	&__body{
		display: flex;
		justify-content: space-between;
		@media (max-width: 1280px){
			height: 100%;
		}
		@media (max-width: 1182px){ 
			img{
				width: 400px;
				height: 80%;
			}
		}
		@media (max-width: 992px){
			flex-direction: column;
			justify-content: center;
			gap: 50px;
			img{
				width: 100%;
				height: 100%;
			}
		}
		@media (max-width: 400px){
			.item{
				max-width: 100%;
				&__number{
					margin-right: 20px;
					width: 72px;
					height: 40px;
					p{
						font-size: 22px;
					}
				}
			}
		}
		.process__item{
			max-width: 620px;
			margin-right: 30px;
			@media (max-width: 1280px){
				max-width: 560px;
			}
			@media (max-width: 1110px){
				max-width: 450px;
				.subtext{
					margin: 16px auto 32px auto !important;
				}
			}
			@media (max-width: 992px){
				max-width: 100%;
				text-align: center;
				margin-right: 0;
			}
			.steps__text{
				margin-bottom: 50px;
			}
			.subtext{
				margin: 16px 0px 32px 0px;
				max-width: 420px;
				color: #1E1B1B60;
			}
			.item{
				max-width: 440px;
				@media (max-width: 992px){
					margin: 0 auto;
				}
				@media (max-width: 768px){
					display: flex;
					flex-direction: column;
					gap: 10px;
					text-align: center;
					align-items: center;
					margin-bottom: 50px;
				}
				&__number{
					width: 72px;
					height: 72px;
					display: flex;
					justify-content: center;
					align-items: center;
					background: #661CE7;
					border-radius: 50%;
					position: relative;
					top: -15px;
					@media (max-width: 1110px){
						margin-right: 20px;
					}
					@media (max-width: 768px){
						top: 0;
						margin: 0 auto;
					}
				}
				&__text{
					@media (max-width: 450px){
						max-width: 100%;
					}
				}
			}
		}
	}
}