.info{
	margin-top: 100px; 
	&__body{
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width: 992px){
			flex-direction: column;
			justify-content: center;
		}
		.info__form{
			max-width: 624px;
			@media (max-width: 992px){
				max-width: 100%;
				margin-bottom: 50px;
				.button{
					margin: 32px auto 0px auto;
				}
				h1{
					text-align: center;
					margin: 0px auto;
				}
			}
			h1{
				max-width: 507px;
				margin-bottom: 48px;
			}
		}
		.info__address{
			max-width: 479px;
			margin-left: 30px;
			@media (max-width: 992px){
				margin: 0px auto;
				text-align: center;
			}
			.address{
				margin-bottom: 32px;
				.text{
					color: rgba(35, 27, 27, 0.7);
				}
			}
		}
	}
}