*, *::before, *::after {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}
ul[class], ol[class] {
	padding: 0;
}
:focus-visible{
	outline: none;
}
::-webkit-scrollbar{
	width: 8px;
}
::-webkit-scrollbar-thumb{
	background-color: #FF6433;
	border-radius: 8px;
}
h1, h2, h3, h4, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd {
	margin: 0;
}
h1, h2, h3, h4, h5, h6, p, a{
	color: #1E1B1B;
	letter-spacing: -0.03em;
}
body {
	scroll-behavior: smooth;
	text-rendering: optimizeSpeed;
	overflow-x: hidden;
	font-family: 'Manrope', sans-serif;
	margin: 0 !important;
	padding: 0 !important;
	opacity: 0;
	transition: all .3s ease;
	&.loaded{
		opacity: 1;
		transition: all .3s ease;
	}
	.overlay{
      width: 100%;
		display: flex;
		flex-direction: column;
		min-height: 100vh;
		height: 100%;
   }
}
.container{
	max-width: $mw;
   margin: 0 auto;
   width: 100%;
   @media (max-width: $md1){
      max-width: 970px;
   }
   @media (max-width: $md2){
      max-width: 750px;
   }
   @media (max-width: $md3){
      max-width: none;
      padding: 0 10px;
   }
}
ul[class], ol[class] {
	list-style: none;
}
a:not([class]) {
	text-decoration-skip-ink: auto;
}
a{
	text-decoration: none;
}
img {
	user-select: none;
}
article>*+* {
	margin-top: 1em;
}
input, button, textarea, select {
	font: inherit;
}
@media (prefers-reduced-motion: reduce) {
	* {
		-webkit-animation-duration: 0.01ms !important;
		animation-duration: 0.01ms !important;
		-webkit-animation-iteration-count: 1 !important;
		animation-iteration-count: 1 !important;
		-webkit-transition-duration: 0.01ms !important;
		transition-duration: 0.01ms !important;
		scroll-behavior: auto !important;
	}
}
// main
h1{
	font-weight: 800;
	font-style: normal;
	font-size: 64px;
	line-height: 1.3;
	@media (max-width: 1280px){
		font-size: calc(30px + (60 - 30) * ((100vw - 300px) / (1280 - 300)));
	}
}
h2{
	font-weight: 800;
	font-style: normal;
	font-size: 58px;
	line-height: 1.3;
	@media (max-width: 1280px){
		font-size: calc(30px + (58 - 30) * ((100vw - 300px) / (1280 - 300)));
	}
}
h3{
	font-weight: bold;
	font-style: normal;
	font-size: 48px;
	line-height: 1.4;
	@media (max-width: 1280px){
		font-size: calc(30px + (48 - 30) * ((100vw - 300px) / (1280 - 300)));
	}
}
h4{
	font-weight: bold;
	font-style: normal;
	font-size: 40px;
	line-height: 1.4;
	@media (max-width: 1280px){
		font-size: calc(28px + (40 - 28) * ((100vw - 300px) / (1280 - 300)));
	}
}
h6{
	font-weight: 600;
	font-style: normal;
	font-size: 20px;
	line-height: 1.5;
	@media (max-width: 1280px){
		font-size: calc(16px + (20 - 16) * ((100vw - 300px) / (1280 - 300)));
	}
}
.title{
	font-weight: bold;
	font-style: normal;
	font-size: 32px;
	line-height: 1.5;
	@media (max-width: 1280px){
		font-size: calc(24px + (32 - 24) * ((100vw - 300px) / (1280 - 300)));
	}
}
.subtitle{
	font-weight: 600;
	font-style: normal;
	font-size: 24px;
	line-height: 1.5;
	@media (max-width: 1280px){
		font-size: calc(18px + (24 - 18) * ((100vw - 300px) / (1280 - 300)));
	}
}
.text{
	font-weight: 600;
	font-style: normal;
	font-size: 18px;
	line-height: 1.75;
	@media (max-width: 1280px){
		font-size: calc(14px + (18 - 14) * ((100vw - 300px) / (1280 - 300)));
		line-height: 1.3;
	}
}
.subtext{
	font-weight: 600;
	font-style: normal;
	font-size: 16px;
	line-height: 1.75;
	@media (max-width: 1280px){
		font-size: calc(12px + (16 - 12) * ((100vw - 300px) / (1280 - 300)));
		line-height: 1.5;
	}
}
.caption{
	font-weight: 500;
	font-style: normal;
	font-size: 16px;
	line-height: 1.5;
	@media (max-width: 1280px){
		font-size: calc(12px + (16 - 12) * ((100vw - 300px) / (1280 - 300)));
	}
}
.button{
	background-color: #FF6433;
	padding: 16px 40px;
	border-radius: 12px;
	color: #FFFFFF;
	transition: all .3s ease;
	&.dark{
		background-color: #1E1B1B;
		&:hover{
			background-color: #000000;
		}
	}
	&:hover{
		background-color: #b74827;
		transition: all .3s ease;
	}
}
section{
	position: relative;
	width: 100%;
	height: auto;
	margin-bottom: 150px;
	@media (max-width: 450px){
		margin-bottom: 100px;
	}
}
.service__items{
	display: grid;
	grid-row-gap: 8px;
	grid-column-gap: 8px;
	.service__item{
		height: 232px;
		display: flex;
		justify-content: center;
		align-items: center;
		cursor: pointer;
		transition: all .3s ease;
		@media (max-width: 1110px){
			height: 180px;
		}
		@media (max-width: 768px){
			height: calc(180px + (220 - 180) * ((100vw - 350px) / (767 - 300)));
		}
		@media (max-width: 350px){
			height: 220px;
		}
		p{
			transition: all .3s ease;
			text-align: center;
		}
		.img{
			display: flex;
			align-items: center;
			justify-content: center;
			margin: 0 auto 16px;
			height: 40px;
			img{
				transition: all .3s ease;
			}
		}
		&:hover{
			background-color: #FF6433;
			transition: all .3s ease;
			p{
				color: #fff;
				transition: all .3s ease;
			}
			img{
				filter: invert(100%) brightness(100%);
				transition: all .3s ease;
			}
		}
	}
}
.link{
	display: flex;
	align-items: center;
	gap: 21px;
	picture{
		display: flex;
		justify-content: center;
		align-items: center;
	}
	img{
		transition: all .3s ease;
	}
	&:hover{
		img{
			transform: translate(10px, 0);
			transition: all .3s ease;
		}
	}
}
.quote__feature{
	display: flex;
	flex-direction: column;
	gap: 32px;
	.feature{
		display: flex;
		justify-content: space-between;
      align-items: flex-start;
		max-width: 385px;
		text-align: left;
	}
}
.process__item{
	.item{
		display: flex;
		justify-content: space-between;
		position: relative;
		height: 165px;
		text-align: left;
		&::after{
			content: '';
			width: 0;
			height: 71px;
			position: absolute;
			left: 35px;
			top: 75px;
			border: 2px dashed #FF6433;
			@media (max-width: 768px){
				display: none;
			}
		}
		&:last-child{
			&::after{
				display: none;
			}
		}
		&__number{
			width: 72px;
			height: 72px;
			display: flex;
			justify-content: center;
			align-items: center;
			background: #661CE7;
			border-radius: 50%;
			position: relative;
			top: -15px;
			@media (max-width: 768px){
				top: 0;
			}
			&::after{
				content: '';
				width: 42px;
				height: 42px;
				position: absolute;
				left: calc(50% - 42px/2	);
				bottom: -20px;
				border-radius: 50%;
				background: rgba(255, 255, 255, 0.24);
			}
			p{
				color: #FFFFFF;
			}
		}
		&__text{
			max-width: 336px;
			.text{
				color: #1E1B1B60;
				margin-top: 8px;
			}
		}
	}
}
.form{
	display: flex;
	flex-direction: column;
	gap: 4px;
	input{
		width: 100%;
		padding: 20px 40px;
		border: none;
		transition: all .3s ease;
		&::placeholder{
			color: #1E1B1B;
		}
		&:focus-visible{
			border: 1px solid #FF6433;
			transition: all .3s ease;
		}
	}
	.button{
		border: none;
		margin-top: 32px;
		width: 206px;
		height: 64px;
		padding: 0;
		cursor: pointer;
	}
}
// main
.main{
	flex: 1 1 100%;
}  