.new{
	@media (max-width: 992px){
		padding-top: 100px;
	}
	img{
		margin: 64px 0px;
		width: 100%;
		@media (max-width: 400px){
			margin: 32px 0px;
		} 
	}
	&__text{
		max-width: 896px;
		margin: 0px auto 80px auto;
		display: flex;
		flex-direction: column;
		gap: 16px;	
		@media (max-width: 400px){
			margin: 0px auto 40px auto;
		}
		.time{
			color: rgba(147, 145, 145, 0.8);
			margin: 0px 0px 16px 0px;
		}
		.subtext, .text{
			color: rgba(35, 27, 27, 0.6);
		}
		.text{
			position: relative;
			margin-left: 32px;
			&::after{
				content: '';
				width: 3px;
				height: 100%;
				background: #1E1B1B;
				position: absolute;
				left: -32px;
				top: 0;
			}
		}
	}
}