.footer{
	background: #1e1b1bf3;
	padding: 128px 0;
	@media (max-width: 992px){
		padding: 50px 0;
	}
	&.-footer{
		height: 400px; 
		position: relative;
		margin-top: 200px;
		padding: 0 !important	;
		@media (max-width: 992px){
			margin-top: 50px;
			img{
				display: none;
			}
		}
		img{
			width: 100%;
			height: 400px;
			object-fit: cover;
		}
		.container.iframe{
			position: absolute;
			width: 100%;
			height: 400px;
			left: 50%;
			transform: translate(-50%, -50%);
			@media (max-width: 992px){
				transform: translate(0, 0);
				position: relative;
				left: 0;
			}
			iframe{
				width: 100%;
				height: 100%;
				border: none;
			}
		}
		@media (max-width: 992px){
			.container{
				max-width: 100%;
				padding: 0 !important;
			}
		}
	}
	&__body{
		display: flex;
		justify-content: space-between;
		@media (max-width: 992px){
			flex-direction: column;
			justify-content: center;
			gap: 60px;
		}
		.footer__item, .footer__address{
			h3, .subtitle{
				color: #fff !important;
				max-width: 463px;		
				@media (max-width: 992px){
					text-align: center;
					margin: 0px auto;
				}	
			}
			.text{
				color: #939191;
				margin-bottom: 8px;
				@media (max-width: 992px){
					text-align: center;
				}
			}
			h3{
				margin-bottom: 64px;	
			}
			.footer__icons{
				display: flex;
				gap: 20px;
				align-items: center;
				@media (max-width: 992px){
					justify-content: center;
					margin-top: 30px;
				}
				a{
					transition: all .3s ease;
					&:hover{
						opacity: 0.7;
						transition: all .3s ease;
					}
				}
			}
			.address{
				margin-bottom: 32px;
				@media (max-width: 992px){
					text-align: center;
					margin: 0px auto 32px;
				}
			}
		}
	}
}
.last{
	position: relative;
	width: 100%;
	background: #1E1B1B;
	padding: 64px 0;
	@media (max-width: 992px){
		padding: 30px 0;
	}
	&__body{
		display: flex;
		justify-content: space-between;
		align-items: center;
		@media (max-width: 992px){
			flex-direction: column;
			justify-content: center;
			gap: 20px;
		}
		.last__menu{
			display: flex;
			justify-content: center;
			align-items: center;
			gap: 45px;
			margin: 0px auto;
			list-style: none;
			@media (max-width: 480px){
				gap: 20px;
			}
			@media (max-width: 350px){
				flex-direction: column;
				gap: 5px;
			}
			li{
				display: inline-block;
				position: relative;
				a{
					color: #fff;
				}
				&::after{
					content: '';
					position: absolute;
					left: 0;
					bottom: -1px;
					width: 100%;
					height: 2px;
					background-color: #fff;
					transform: scale(0);
					transition: all .3s ease;
				}
				&:hover::after{
					transform: scale(1);
					transition: all .3s ease;
				}
			}
		}
		.last__text{
			p{
				color: #939191;
				@media (max-width: 992px){
					text-align: center;					
				}
			}
		}
	}
}