.quote{
	&__body{ 
		display: flex;
		justify-content: space-between;
		gap: 50px;
		@media (max-width: 992px){
			flex-direction: column;
			gap: 100px;
		}
		.quote__item{
			@media (max-width: 992px){
				text-align: center;
			}		
			&:last-child{
				h3{
					max-width: 587px;
					margin-bottom: 32px;
					@media (max-width: 992px){
						text-align: center;
						max-width: 100%;
					}
				}
				.form{
					.button{
						@media (max-width: 992px){
							margin: 32px auto 0px auto;
						}
					}
				}
			}
			.quote__feature{
				margin-top: 48px;
				a{
					align-items: center;
					img{
						width: 52px;
						height: auto;
					}
				}
				@media (max-width: 992px){
					margin: 48px auto 0px auto;
					width: 385px;
					a{
						margin: 0px auto;
					}
					.feature{
						display: flex;
						flex-direction: column;
						justify-content: center;
						text-align: center;
						img{
							margin: 0 auto;
							display: flex;
							justify-content: center;
						}
					}
				}
				@media (max-width: 400px){
					width: 100%;
				}
			}
		}
	}
}