.cta{
	position: relative;
	display: flex;
	justify-content: space-between;
	width: 100vw;
	height: 502px;
	object-fit: cover;
	@media (max-width: 1280px){
		height: 400px;
	}
	&__default{
		margin: 0 !important;
	}
	&__img{
		width: 30%;
		max-width: 100%;
		&:last-child{
			width: 70%;
		}
		@media (max-width: 992px){
			display: none;
		}
		img{
			width: 100%;
			height: 100%;
			object-fit: cover;
			user-select: none;
		}
	}
	&__body{
		position: absolute;
		height: 100%;
		width: 700px; 
		left: 21%;
		background-color: #FF6433;
		display: flex;
		justify-content: center;
		align-items: center;
		@media (max-width: 992px){
			width: 100%;
			left: 0;
			padding: 0px 20px;
		}
		.cta__text{
			max-width: 569px;
			@media (max-width: 992px){
				text-align: center;
			}
			h3{
				color: #fff;
				margin-bottom: 48px;
			}
		}
	}
}