.services{
	&__body{
		display: flex;
		justify-content: space-between;	
		@media (max-width: 992px){
			flex-direction: column-reverse;
			align-items: center;
			justify-content: center;
		}
		.service__items{
			grid-template-columns: repeat(2, 1fr);
			grid-template-rows: repeat(3, 1fr);
			grid-row-gap: 32px;
			grid-column-gap: 32px;
			@media (max-width: 1182px){
				grid-row-gap: 10px;
				grid-column-gap: 10px;
			} 
			@media (max-width: 992px){
				display: flex;
				align-items: center;
				gap: 10px;
				overflow-x: scroll !important;
				width: 100%;
				&::-webkit-scrollbar{
					display: none;
				}
			}
			.service__item{
				width: 296px;
				cursor: pointer;
				@media (max-width: 1280px){
					width: 240px;
				}
				@media (max-width: 1110px){
					width: 200px;
				}
				@media (max-width: 992px){
					min-width: 240px;
				}
				&.show{
					background-color: #FF6433;
					p{
						color: #fff;
					}
					img{
						filter: invert(100%) brightness(100%);
					}
				}
			}
		}
		.services__info{
			width: 50%;
			height: 100%;
			@media (max-width: 992px){
				width: 600px;
				margin: 0px auto;
				grid-area: 2 / 1 / 3 / 2; 
			}
			@media (max-width: 768px){
				width: 100%;
			}
			.services__item{
				padding: 64px 48px;
				display: none;
				&.show{
					display: block;
				}
				@media (max-width: 768px){
					max-width: 600px;
					margin: 0px auto;
				}
				@media (max-width: 450px){
					padding: 48px 0;
				}
				h3{
					margin: 32px 0px 16px 0px;
				}
				p{
					color: #232536b4;
					margin-bottom: 32px;
				}
				.item__img{
					width: 100%;
					height: auto;
				}
			}
		}
	}
}