.process{
	&__body{
		display: flex;
      justify-content: space-around;
      h2{
         max-width: 550px;
      }
		@media (max-width: 1110px){
         flex-direction: column;
			justify-content: center;
			gap: 50px;
         h2{
            margin: 0 auto;
         }
		}
		.process__item{
			@media (max-width: 1110px){
				text-align: center;
				width: 100%;
				margin-bottom: 80px;
				.subtext{
					margin: 16px auto 32px auto !important;
				}
			}
			.subtext{
				margin: 16px 0px 32px 0px;
				max-width: 420px;
				color: #1E1B1B60;
			}
			&:last-child{
				display: flex;
				flex-direction: column;
				gap: 16px;
				@media (max-width: 1110px){
					max-width: 432px;
					margin: 0 auto;
				}
				@media (max-width: 768px){
					gap: 40px;
				}
			}
			.item{
				@media (max-width: 768px){
					display: flex;
					flex-direction: column;
					gap: 10px;
					text-align: center;
					align-items: center;
				}
				&__number{ 
					width: 72px;
					height: 72px;
					display: flex;
					justify-content: center;
					align-items: center;
					background: #661CE7;
					border-radius: 50%;
					position: relative;
					top: -15px;
					margin-right: 30px;
					@media (max-width: 1110px){
						margin-right: 20px;
					}
					@media (max-width: 768px){
						top: 0;
						margin: 0 auto;
					}
				}
				&__text{
					@media (max-width: 450px){
						max-width: 100%;
					}
				}
			}
		}
	}
}