.solution{
	height: 629px;
	@media (max-width: 1110px){
		height: auto;
	}
	&__body{ 
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		width: 100%;
		@media (max-width: 1110px){
			flex-direction: column;
			height: auto;
		}
		.solution__item{
			flex: 0 1 33.33%;
			background-size: cover !important;
			width: 100%;
			height: 100%;
			background: url(../img/main/cta_bg-2.webp) center no-repeat;
			@media (max-width: 1110px){
				flex: 1 0 100%;
				text-align: center;
			}
			.solution__text{
				width: 100%;
				height: 100%;
				display: flex;
				justify-content: center;
				align-items: center;
				padding: 135px 20px;
				@media (max-width: 480px){
					padding: 50px 20px;
				}
				.content{
					max-width: 437px;
					h4{
						color: #fff;
						margin-bottom: 32px;
					}
					p{
						color: rgba(255, 255, 255, 0.7);
					}
				}	
			}
			&:first-child{
				background: url(../img/about/process_img.webp) center no-repeat;
				.solution__text{
					background: rgba(248, 91, 41, 0.9);
				}
			}
			&:nth-child(2){
				.solution__text{
					background: rgba(0, 0, 0, 0.85);
				}
			}
			&:last-child{
				.solution__text{
					background: rgba(116, 67, 202, 0.9);
				}
			}
		}
	}
}