.article{
	h1{
		margin-bottom: 96px;
		@media (max-width: 768px){
			margin-bottom: 40px;
		}
	}
	&__body{
		display: flex;
		justify-content: space-between;
		@media (max-width: 768px){
			flex-direction: column-reverse;
			justify-content: center;
			align-items: center;
			gap: 80px;
		}
		.article__aside{
			width: 330px;
			@media (max-width: 768px){
				width: 100%;
				height: auto; 
				.service__items{
					width: 100% !important;
					overflow-y: scroll !important;
					display: flex;
					.service__item{
						min-width: 240px;
					}
					&::-webkit-scrollbar{
						display: none;
					}
				}
				.feature{
					margin: 0px auto !important;
					display: flex;
					flex-direction: column;
					align-items: center;
					gap: 10px;
					text-align: center;
				}
			}
			.title{
				margin-bottom: 32px;
			}
			.service__items{
				width: 264px;
				margin: 0px auto 80px auto;
			}
		}
		.article__content{
			max-width: 854px;
			margin-left: 30px;
			@media (max-width: 768px){
				margin-left: 0;
			}
			img{
				width: 100%;
			}
			.article__text{
				margin-top: 56px;
				@media (max-width: 450px){
					margin-top: 20px;
				}
				.text__section{
					display: flex;
					flex-direction: column;
					gap: 24px;
					margin-bottom: 68px;
					.text, .subtext{
						color: #232536b4;
					}
					.text{
						position: relative;
						margin-left: 32px;
						&::after{
							content: '';
							width: 3px;
							height: 100%;
							background: #1E1B1B;
							position: absolute;
							left: -32px;
							top: 0;
						}
					}
				}
			}
		}
	}
}