.provide{
	h2{
		max-width: 861px;
		text-align: center;
		margin: 0px auto 64px auto;
	}
	.quote__feature{
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: row;
		flex-wrap: wrap;
		gap: 0px; 
		@media (max-width: 1182px){
			gap: 30px;
		}
		@media (max-width: 768px){
			gap: 20px;
			.feature{
				margin: 0 auto;
			}
		}
		.feature{
			display: flex;
			flex-direction: column;
			text-align: center;
			@media (max-width: 1110px){
				padding: 0px 10px;
			}
			img{
				margin: 0px auto 24px auto;
			}
			.text{
				color: rgba(35, 37, 54, 0.6);
			}
		}
	}
}