.blog{
	&__body{
		display: flex;
		justify-content: space-between;
		@media (max-width: 992px){
			display: block;
		}
		.time{
			color: rgba(147, 145, 145, 0.8);
		}
		.blog__news{
			width: 537px;
			padding: 32px;
			.link{
				img{
					height: 18px;
				}
			}
			.news__img{
				width: 100%;
				height: auto;
			}
			@media (max-width: 992px){
				width: 100%;
				margin-bottom: 40px;
			}
			@media (max-width: 400px){
				padding: 0px;
			}
			.time{
				margin: 32px 0px 16px 0px;
			}
			p:nth-child(3){
				margin: 16px 0px 32px 0px;
				color: rgba(35, 37, 54, 0.6);
			}
		}
		.blog__items{
			display: flex;
			flex-direction: column;
			gap: 32px;
			max-width: 694px;
			@media (max-width: 992px){
				margin: 0px auto;
			}
			.blog__item{
				display: flex;
				justify-content: space-between;
				align-items: center;
				max-width: 694px;
				height: 100%;
				transition: all .3s ease; 
				&:hover{
					.title{
						text-decoration: underline;
					}
				}
				@media (max-width: 1110px){
					max-width: 600px;
					img{
						height: auto;
						width: auto;
					}
				}
				@media (max-width: 768px){
					max-width: 100%;
					img{
						height: 100%;
						width: 150px;
					}
					.title{
						font-size: calc(20px + (40 - 20) * ((100vw - 300px) / (1280 - 300)));
					}
				}
				@media (max-width: 450px){
					img{
						height: 100%;
						width: 100px;
					}
					.content{
						margin-left: 10px !important;
					}
				}
				.content{
					margin-left: 30px;
					.time{
						margin-top: 4px;
					}
				}
			}
		}
	}
}